<template>
    <div class="app-goodlist">
        <header id="heade">
            <nav>
                <a id="cation" @click="back" class="mui-icon mui-icon-back mui-icon-left-nav mui-pull-left"></a>
                <a href="">
                    <span  id="logo"></span>
                </a>
                <a href="" class="navs mui-icon mui-icon-bars mui-icon-right-nav mui-pull-right">
                    
                </a>
            </nav>
        </header>
        <!--  start -->
        <div class="goods-item" v-for="(item,i) of list" :key="i">
            <!-- GoodList.vue -->
                <img :src="item.img_url"  @click.prevent="jumpInfo" :data-lid="item.lid"/>
                <div class="lmo1">
                    <div class="jingxuan1"><a href="#">{{item.bk}}</a></div>
                    <h4>{{item.title}}</h4>
                    <div class="cart">
                        <h3>{{item.price}}</h3>
                    </div>
                </div>
        </div>
        <!--  end -->
        <!--  -->
        <mt-button size="large" type="primary" style="background:#ccc" @click="looksMore"></mt-button>
    </div>
</template>
<script>
export default {
    data(){
        return {
            list:[],   //
            pno:0   //
        }
    },
    created(){
        //
        this.looksMore();
    },
    methods:{
        back(){
            this.$router.go("-1");
        },
        //
        jumpInfo(e){
            var lid=e.target.dataset.lid;
            this.$router.push("/Cakeinfo?lid="+lid);
        },
        //
        looksMore(){
            //0.
            this.pno++;
            //console.log(123);
            //1.url
            var url = "cake?pno="+this.pno;
            //2.ajax
            this.axios.get(url).then(result=>{
                var rows = this.list.concat(result.data.data);
                this.list = rows;
                //console.log(this.list);
            })
        }
    },
}
</script>
<style>
/*  */
.app-goodlist{
    display: flex;
    flex-wrap: wrap;/**/
    justify-content: space-between;/**/
    padding: 4px;
}
/*  */
.app-goodlist .goods-item{
    width:49%;
    border: 1px solid #ccc;
    margin: 2px 0;
    padding: 2px;
    display: flex;
    flex-direction: column;
    min-height: 245px;
    position: relative;
    top:40px;
}
/*  */
.app-goodlist .goods-item img{
    width: 100%;
}
.jingxuan1{
        width: 80px;
        height: 25px;
        border: 1px solid #ff894c;
        border-radius: 15px 15px;
        margin-top: 15px;
        margin-left: 7px;
}
.jingxuan1 a{
    color: #ff894c;
}
</style>



