<template>
    <div class="app-shopcart">
        <header id="heade">
            <nav>
                <a id="cation" @click="back" class="mui-icon mui-icon-back mui-icon-left-nav mui-pull-left"></a>
                <a href="">
                    <span  id="logo"></span>
                </a>
                <a href="" class="navs mui-icon mui-icon-bars mui-icon-right-nav mui-pull-right">
                    
                </a>
            </nav>
        </header>
            <!-- card.html -->
            <div class="mui-card">
				<div class="mui-card-header">
                    <!--  -->
                    <input type="checkbox" @click="selectAll">
                </div>
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
						<!--  -->	    			                  
						<div class="mui-media-body" v-for="(item,i) of list" :key="i" style="height:100px">
                            <input type="checkbox" id="checkbox" :data-id="item.id" :checked="item.cb" :data-i="i" @click="modifyItem">
                            <img id="roses" :src="item.img_url">
                            <div class='mui-ellipsis'>
                                <div class="title">{{item.title}}</div>
                            </div>
                            <div class='mui-ellipsis1'>
                                <div class="price">{{item.price}}</div>
                                <button @click="handleReduce(i)" id="btn1">-</button>
                                <span class="price1">{{item.count}}</span>
                                <button @click="handleAdd(i)" id="btn2">+</button>
                                <input type="button" value="x" id="rem" :data-id="item.id" @click="removeItem" :data-idx="i">
                            </div>
					    </div>
                        <!--  -->
				    </div>
			    </div>
                <!--  -->
				<div class="mui-card-footer">
                    <input type="button" @click="delItem" value="">    
                </div>
			</div>
            <nav class="mui-bar mui-bar-tab">
                <a class="mui-tab-item mui-active" href="#">
                    <span class="mui-tab-label">:<p style="color:#ff894c">{{total}}</p></span>
                </a>
                <a class="mui-tab-item" href="#" style="background:#ff894c">
                    <span class="mui-tab-label"></span>
                </a>
            </nav>
    </div>    
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return {
            list:[],//
            alicb:false,//  
            total:0,
            count:{},
        }
    },
    created(){
        this.loadMore();
    },
    methods:{
        back(){
            this.$router.go("-1");
        },
        // 
        handleReduce(i){
            if(this.list[i].count==1)return;
            this.list[i].count--;
        },
        // 
        handleAdd(i) {
            this.list[i].count++;
        },
        //
        selectAll(e){
            //console.log(123);
            //1.
            var rs=e.target.checked;
            //2.
            for(var item of this.list){
                item.cb=rs;
            }
            //3.item.cb=
            //4.
            this.allcb=rs;
        },
        modifyItem(e){
            //1. true false
            var rs=e  .target.checked;
            //console.log(rs);
            //2.
            var i=e.target.dataset.i;
            //console.log(i);
            //3.cb
            this.list[i].cb=rs;
            //console.log(this.list);
            //  true false
            //1.
            var size1=this.list.length;
            //2.cb true
            var count=0;
            for(var item of this.list){
                if(item.cb){
                    count++;
                }
            }
            //console.log(size1+":"+count);
            //3.==cb true 
            if(count==size1){
                this.allcb=false;
            }else{
                this.allcb=true;
            }
            //4.allcb=true
            //5.allcb=false
        },
        //
        delItem(){
            //console.log("...");
            //[{cb:true,id:1},{cb:true,id:3}]
            //"1,3"
            //1:
            var html="";
            //2:
            for(var item of this.list){
                //3.cb==true
                if(item.cb){
                    //4:id
                    html+=item.id+",";
                }
            }
            if(html.length==0){
                Toast("");
                return;
            }
            html=html.substring(0,html.length-1);
            var url="removeMItem?ids="+html;
            this.axios.get(url).then(result=>{
                if(result.data.code==1){
                    Toast("");
                    this.loadMore();
                }else{
                    Toast("");
                }
            })
        },
        //
        removeItem(e){
            var id=e.target.dataset.id;
            var idx=e.target.dataset.idx;
            var url="removeItem";
            var postData=this.qs.stringify({
                id:id
            });
            this.axios.post(url,postData).then(result=>{
                if(result.data.code==1){
                    this.list.splice(idx,1);
                    Toast("");
                }else{
                    Toast("");
                }
            })
        },
        //
        loadMore(){
            var url="getShopCart";
            this.axios.get(url).then(result=>{
                var rows=result.data.data;
                for(var item of rows){
                    item.cb=false;
                }
                this.list=rows;
                //console.log(this.list);
            });    
        }
    },
}
</script>
<style>
#checkbox{
    position: relative;
    top: -30px;
}
.price1{
    display: inline-block;
    width: 20px;
    height: 30px;
    text-align: center;
}
.mui-ellipsis{
    position: relative;
    top:-77px;
    left:118px;
}
.mui-ellipsis div{
    width: 178px;
    height: 20px;
    overflow: hidden;
}
.mui-ellipsis input:last-child{
    width: 30px;
    height: 30px;
    margin-left:16px;
}
.num{
    width: 30px;
}
.mui-ellipsis button{
    width: 30px;
    height: 30px;
    margin-left: 0px;
}
.price{
    position: relative;
    top:-71px;
    left: 110px;
    color: orangered;
    font-size: 16px;
}
#roses{
    width: 60px;
    height: 60px;
}
#btn1,.price1,#btn2{
    position: relative;
    top:-100px;
    left: 205px;
}
#rem{
    border: none;
    margin-left: 300px;
    margin-top: -130px;
}
#roses{
    width: 80px;
    height: 80px;
    margin-left: 20px;
}

</style>



