<template>
    <div class="app-Cakeinfo">
        <!--  -->
        <header id="heade">
            <nav>
                <a id="cation" @click="back" class="mui-icon mui-icon-back mui-icon-left-nav mui-pull-left"></a>
                <a href="">
                    <span  id="logo"></span>
                </a>
                <a href="" class="navs mui-icon mui-icon-bars mui-icon-right-nav mui-pull-right">
                    
                </a>
            </nav>
        </header>
        <div class="mui-card">
            <div class="lmo1">
                <img style="width:100%;height:309px" :src="info.img_url"/>
                <h4>{{info.title}}</h4>
                <div class="jingxuan"><a href="#">{{info.bk}}</a></div>
                <div class="cart">
                    <h3>{{info.price}}</h3>
                    <h5>{{info.sale}}</h5>
                </div>
            </div>
		</div>
        <!--  -->
        <div class="mui-card">
            <div>
                <dt>:</dt>
                <dd>{{info.cailiao}}</dd>   
            </div>
            <div>
                <dt>:</dt>
                <dd>{{info.save}}</dd>   
            </div>
            <div>
                <dt>:</dt>
                <dd>{{info.song}}</dd>
            </div>
            <div>
                <dt>:</dt>
                <dd>{{info.say}}</dd>
            </div>
        </div>
        <div class="mui-card">
            <p class="mui-icon mui-icon-location"></p>
        </div>
        <div class="mui-card">
            <h4></h4>
            <p></p>
        </div>
        <div class="mui-card">
			<div class="mui-card-content">
				<div class="mui-card-content-inner">
					<textarea  placeholder="" maxlength="120" v-model="msg"></textarea>
                    <mt-button style="background:#ff894c;" size="small" type="primary" @click="addComment"></mt-button>
				</div>
			</div>
			<div class="mui-card-footer"></div>
		</div> 
      <!-- end--> 
      <!-- start--> 
      		<div class="mui-card">
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
						<div class="cmt-list">
                            <div class="cmt-item" v-for="(item,i) of list" :key="i">
                                <div> {{i+1}} : {{item.ctime | datetimeFilter}}</div>
                                <div>:{{item.content}}</div>
                            </div>
                        </div>
					</div>
				</div>
			</div>
            <div class="mui-card">
                <footer>
                <div id="label1">
                <a href="#" class="footer-links-item"></a>
                <a href="#" class="footer-links-item"></a>
                <a href="#" class="footer-links-item"></a>
                <a href="#" class="footer-links-item"></a>
            </div>
            <div id="label2">
                <a href="#" class="footer-links-item"></a>
                <a href="#" class="footer-links-item"></a>
                <a href="#" class="footer-links-item"></a>
                <a href="#" class="footer-links-item"></a>
            </div>
            <div>
                <p id="footer">
                    Copyright  2005~2019 <br>
                </p>
            </div>
        </footer>
            </div>
            <nav class="mui-bar mui-bar-tab">
                    <a class="mui-tab-item mui-active" href="#tabbar">
                        <span class="mui-icon-extra mui-icon-extra-custom"></span>
                        <span class="mui-tab-label"></span>
                    </a>
                    <a class="mui-tab-item" href="#tabbar-with-chat">
                        <span class="mui-icon-extra mui-icon-extra-cart"></span>
                        <span class="mui-tab-label"></span>
                    </a>
                    <a style="background:#435448;" class="mui-tab-item">
                        <span class="mui-tab-label buy" @click="addcart"></span>
                    </a>
                    <a style="background:#ff894c" class="mui-tab-item">
                        <span class="mui-tab-label buy"></span>
                    </a>
            </nav>
    </div>    
</template>
<script>
import {Toast} from "mint-ui"
  export default {
    data(){
      return {
        list:[],     //
        pno:0,       //
        pageSize:5,  //
        msg:"",
        lid:this.$route.query.lid,
        info:{},
      }
    },
    created(){
      this.loadMore();
      this.findProd();
      //console.log(this.lid)    
    },
    methods:{
        back(){
            this.$router.go("-1");
        },
        //
        addcart(){
            var pid=this.lid;
            var title=this.info.title;
            var price=this.info.price;
            var img=this.info.img_url;
            var count=this.info.count;
            var url="addcart?pid="+pid+"&img_url="+img+"&title="+title+"&price="+price+"&count="+count;
            this.axios.get(url).then(result=>{
                //console.log(result);
                
            })
        },
        findProd(){
            var url="findProd?lid="+this.lid;
            this.axios.get(url).then(result=>{
                //console.log(result)
                this.info=result.data[0];
            })
        },
      loadMore(){
        this.pno++;
        var ps = this.pageSize;
        var reg = /^[0-9]{1,}$/;
        if(!reg.test(this.pno)){
          Toast("");
          return;
        }
        var url = "";
        url+="getComment?nid="+this.nid;
        url+="&pno="+this.pno;
        this.axios.get(url).then(result=>{
          this.list = result.data.data;
        })
      },
      //
      addComment(){
        var nid = this.nid;
        var con = this.msg;
        var size = con.trim().length;
        if(size==0){
          Toast("");
          return;
        }
        var postData = this.qs.stringify({
              nid:nid,
              content:con
        });
        var url = "";
        url+="/addcomment";
        this.axios.post(url,postData).then(result=>{
            Toast("");
            this.msg = "";
            this.pno=0;//
            this.list=[];//
            this.loadMore();//
        })    
      },

    }
  }
</script>
<style>
.cart{
    float: left;
}
.cart h5{
    margin-left: 190px;
}
.mui-card{
    position: relative;
    top:30px;
}
.mui-card div dd{
    color: gray;  
}
.buy{
    color: white;
}
.mui-tab-label{
    font-size: 14px;
}
 .footer-links-item{
        display: block;
        flex: 1;
        padding: 0.28571429rem 0.57142857rem;
        background: #e9ecf0;
        font-size: 0.85714286rem;
        text-align: center;
        color: #000;
    }
    #footer{
       height: 100px;
        font-size: 10px;
        text-align: center;
    }
</style>



